 body{
    font-family: sans-serif;
    letter-spacing: 0.06rem;
  }
  body p{
    color:rgba(46, 42, 57, 0.75);
  }
  .page-width {
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 1.5rem;
  }
  .main-section{
    padding: 80px 0;
  }
  .text-center{
    text-align: center;
  }
  .pro_vid_img_sec {
    background-color: #fafafa;
    padding-top: 50px;
    padding-bottom: 50px;
  }
  .pro_vid_img_row {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    gap: 30px;
    justify-content: space-between;
  }
  .pro_vid_img_row .vid_img_box {
    position: relative;
  }
  .pro_vid_img_sec .vid_img_box video {
    width: 100%;
    border-radius: 25px;
  }
  .pro_vid_img_row .vid_img_box, .pro_vid_img_row .text_box {
    width: calc(50% - 30px);
  }
  .pro_vid_img_row .text_box h3 {
    font-size: 40px;
    line-height: 45px;
    font-weight: 700;
    margin-top: 0;
    color: #2e2a39;
    margin-bottom: 10px;
    text-align: left;
  }
  .pro_icon_text {
    margin-bottom: 10px;
  }
  .pro_vid_img_sec .item {
    display: flex;
    align-items: center;
    padding: 10px 0;
    border-bottom: 1px solid #e8e7e7;
  }
  .pro_vid_img_sec .text_box .item .text {
    max-width: 295px;
    margin-left: 50px;
    text-align: left;
    color:rgba(46, 42, 57, 0.75);
  }
  .pro_vid_img_sec .text_box .icon_progress {
    width: 70px;
    height: 70px;
  }
  .b-product-card__benefits-asterisk p {
    font-size: 14px;
    line-height: 1.2;
    margin: 10px 0 20px;
    text-align: left;
  }
  [role=progressbar] {
    --percentage: var(--value);
    --primary: #7069bc;
    --secondary: #e3d2fd;
    --size: 300px;
    animation: progress 2s .5s forwards;
    width: 70px;
    aspect-ratio: 1;
    border-radius: 50%;
    position: relative;
    overflow: hidden;
    display: grid;
    place-items: center;
  }
  [role=progressbar]:before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: conic-gradient(var(--primary) calc(var(--percentage) * 1%),var(--secondary) 0);
    mask: radial-gradient(white 55%,transparent 0);
    mask-mode: alpha;
    -webkit-mask: radial-gradient(#0000 55%,#000 0);
    -webkit-mask-mode: alpha;
  }
  [role=progressbar]:after {
    counter-reset: percentage var(--value);
    content: counter(percentage) "%";
    font-size: 23px;
    color: var(--primary);
  }
  a.btn {
    display: inline-block;
    vertical-align: middle;
    background: #7069bc;
    font-size: 17px;
    color: #fff;
    padding: 10px 35px;
    text-align: center;
    text-transform: uppercase;
    border-radius: 4px;
    border: none;
    font-weight: 700;
    letter-spacing: 0px;
    text-decoration: none;
  }
  .vid_img_btn{
    text-align: left;
  }
  .vid_img_btn a.btn span {
    background-position: left center;
    background-repeat: no-repeat;
    background-size: 30px auto;
    padding-left: 40px;
  }
  .product-inner{
   display: flex;
   gap: 50px;
   flex-wrap: wrap;
  }
  .product-inner .product__title{
    font-size: 40px;
    font-weight: 700;
  }
  .product-inner ul{
    padding: 0;
  }
  .product-inner ul li{
    list-style: none;
    color:rgba(46, 42, 57, 0.75);
  }
  .product__media-wrapper{
    width: 50%;
  }
  .product__media-wrapper img{
    border-radius: 10px;
    cursor: pointer;
  }
  .product__info-wrapper{
    width: calc(50% - 50px);
    text-align: left;
  }
  .thumbnails-images img, .product-images img{
    width:100%;
  }
  .thumbnails-images .slick-current .product__media-item img{
    border: 1px solid #000;
  }
  .thumbnails-images .product__media-item{
    padding-right: 10px;
  }
  .kaching-bundles .kaching-bundles__bars {
    display: flex;
    flex-direction: column;
  }
  .kaching-bundles .kaching-bundles__bar {
    display: flex;
    flex: 1;
    margin: 5px 0;
  }
  .kaching-bundles__bar-container {
      box-shadow: inset 0 0 0 1px rgba(112, 105, 188, 0.3);
      display: flex;
      flex-direction: column;
      justify-content: center;
      position: relative;
      line-height: 25px;
      padding: 10px 20px;
      box-sizing: border-box;
      cursor: pointer;
      width: 100%;
      min-height: 75px;
      background-color: #fff;
      margin: 0;
      border-radius: 10px;
  }
  .kaching-bundles__bar-main {
    display: flex;
    align-items: center;
    gap: 18px;
  }
  .kaching-bundles__bar-radio {
    box-shadow: 0 0 0 2px rgba(112, 105, 188, 0.3);
    display: flex;
      flex-shrink: 0;
      width: 20px;
      height: 20px;
      border-radius: 50%;
      background: #FFF;
      border: 3px solid #FFF;
      box-sizing: border-box;
  }
  .kaching-bundles__bar-price{
    font-weight: 700;
      font-size: 18px;
  }
  .kaching-bundles__bar-full-price{
    font-size: 16px;
  }
  .kaching-bundles__bar-content {
    align-items: center;
    justify-content: space-between;
    display: flex;
    gap: 8px;
    flex: 1;
  }
  .kaching-bundles__bar-content-left {
    display: flex;
    flex-direction: column;
    flex: 1;
    align-items: flex-start;
  }
  .kaching-bundles__bar-first-line {
    display: flex;
    flex-wrap: wrap;
    column-gap: 8px;
  }
  .kaching-bundles__bar-title {
    font-size: 18px;
    font-style: normal;
    color: #000;
    display: flex;
    align-items: center;
    gap: 8px;
    font-weight: 700;
  }
  .kaching-bundles__bar-container input[type=checkbox] {
    display: none;
  }
  .kaching-bundles .kaching-bundles__block-title:after, .kaching-bundles .kaching-bundles__block-title:before {
    content:'';
    background-color: rgba(112, 105, 188, 0.3);
    content: "";
      flex-grow: 1;
      height: 2px;
  }
  .kaching-bundles .kaching-bundles__block-title {
    display: flex;
    align-items: center;
    gap: 8px;
    font-weight: 700;
    font-size: 14px;
    margin-bottom: 10px;
    color: #000;
  }
  .kaching-bundles__bar-container.checked{
    box-shadow: inset 0 0 0 2px rgba(112, 105, 188, 1);
  }
  .kaching-bundles__bar-container.checked .kaching-bundles__bar-radio{
    background: rgba(112, 105, 188, 1);
      box-shadow: 0 0 0 2px rgba(112, 105, 188, 1);
  }
  .kaching-bundles__bar-subtitle{
    font-size: 14px;
  }
  button.product-form__submit.button.button--full-width.button--primary {
    min-height: 65px;
    background-color: #7069bc;
    font-weight: 700;
    text-transform: capitalize;
    font-size: 20px;
    width: 100%;
    border-radius: 10px;
    border: none;
    color: #fff;
  }
  .product-form__buttons {
    max-width: 44rem;
    margin-bottom: 20px;
  }
  .product-form__buttons .outer-fingerImage{
    vertical-align: middle;
  }
  .product-form__buttons .fingerImage{
    width: 30px;
    padding-right: 5px;
  }
  .pro-iwt-wrap {
    display: flex;
  }
  .pro-iwt-wrap .innr {
    width: 33.33%;
    text-align: center;
  }
  .pro-iwt-wrap .innr p{
    color: #000;
    margin: 0;
  }
  .pro-iwt-wrap .inner-icon img {
    width: 80px;
    height: 80px;
    margin: 0 auto;
  }

  @media (max-width: 767px) {
    .pro_vid_img_row .text_box h3, .com_left-side h2.com_heading {
        font-size: 36px;
        line-height: 41px;
    }
    .pro_vid_img_row .vid_img_box, .pro_vid_img_row .text_box {
        width: 100%;
    }
    .product__media-wrapper, .product__info-wrapper{
        width: 100%;
    }
  }
